<template>
  <div class="card">
    <h3>{{ info ? info.strDrink : "Drink name" }}</h3>
    <div class="description">
      <div class="description__image image-overlay" v-if="info">
        <img :src="info.strDrinkThumb" alt="" height="300px" />
      </div>
      <div class="description__altpic" v-else>
        <img src="../assets/cocktail.svg" alt="" height="300px" />
      </div>
      <div class="description__compound">
        <h4>Compound:</h4>
        <ul @click.prevent="dblclck()">
          <li
            v-for="(item, i) in compound"
            :key="i"
            @click.prevent="ingredientDetails(item)"
          >
            {{ item }}
          </li>
        </ul>
      </div>
      <div class="description__recipe" v-if="info">
        <p v-if="info.strInstructions">{{ info.strInstructions }}</p>
        <p v-else-if="info.strInstructionsIT">{{ info.strInstructionsIT }}</p>
        <p v-else-if="info.strInstructionsES">{{ info.strInstructionsES }}</p>
        <p v-else-if="info.strInstructionsDE">{{ info.strInstructionsDE }}</p>
        <p v-else>Google it.</p>
        <p class="description__glass" v-if="info.strGlass">
          Served in a {{ info.strGlass }}
        </p>
      </div>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  methods: {
    async ingredientDetails(payload) {
      await this.$store.dispatch("getIngrCard", payload);
      await this.$router.push("/ingredient-card").catch(() => {});
    },
    async dblclck() {
      await event.target.click();
    },
  },
  computed: {
    info() {
      return this.$store.getters.getCardInfo;
    },
    compound() {
      const result = [];
      for (let i in this.info) {
        if (i.includes("Ingredient") && this.info[i]) result.push(this.info[i]);
      }
      return result;
    },
  },
};
</script>

<style lang="scss" >
@import "../scss/mixins.scss";
.card {
  // height: calc(100vh-90px);
  height: 100%;
  padding: 25px 20px 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  h3 {
    @include card-text-pink;
    font-family: "La Belle Aurore";
    font-size: 56px;
    font-weight: bold;
  }
  .description {
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .description__image {
      position: relative;
    }
    .image-overlay::after {
      content: "";
      display: block;
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      background: rgba(240, 43, 224, 0.096);
    }
    .description__compound {
      h4 {
        font-size: 22px;
        margin-bottom: 25px;
        @include card-text-pink;
      }
      ul {
        display: flex;
        flex-direction: column;
        gap: 10px;
        li {
          @include card-text-blue;
        }
      }
    }
    .description__recipe {
      flex-basis: 450px;
      line-height: 1.5;
      @include card-text-blue;
    }
    .description__glass {
      @include card-text-pink;
    }
  }
}
</style>